body {
    margin: 0;
    background-color: rgb(30, 20, 20);
}

main {
    position: absolute;
    min-width: 100%;
    top: 0;
    bottom: 0;
}

.colour-0 {
    color: #aaa;
}

.fill-0 {
    fill: #aaa;
}

.colour-1 {
    color: rgb(20, 146, 196);
}

.fill-1 {
    fill: rgb(20, 146, 196);
}

.stroke-1 {
    stroke: rgb(20, 146, 196);
}

.colour-2 {
    color: rgb(196, 20, 96);
}

.fill-2 {
    fill: rgb(196, 20, 96);
}

.stroke-2 {
    stroke: rgb(196, 20, 96);
}

.colour-3 {
    color: rgb(20, 196, 96);
}

.fill-3 {
    fill: rgb(20, 196, 96);
}

.colour-4 {
    color: rgb(220, 220, 20);
}
  
.fill-4 {
    fill: rgb(220, 220, 20);
}

.current {
    fill: rgb(196, 20, 96);
}

.menu-button {
    position: fixed;
    cursor: pointer;
    width: 5vw;
    height: 5vw;
}

@media (max-aspect-ratio: 1/1) {
    .menu-button {
        width: 4vh;
        height: 4vh;
    }
}

.menu-button circle {
    fill: #ddd;
    stroke: #ddd;
    stroke-width: 0;
    transition: fill 100ms ease-out, stroke 100ms ease-out;
}

.menu-button circle:hover {
    fill: #fff;
    stroke: #fff;
}

.menu-button circle:focus {
    outline: none;
    stroke-width: 1;
    filter: url(#glow-filter);
}

.back-button {
    left: 4px;
    top: 4px;
}

.next-puzzle-button {
    right: 4px;
    top: 4px;
}

