.flashing {
    animation: blinker 1s linear infinite;
}
    
@keyframes blinker {
    50% {
        opacity: 0.2;
    }
}

.graph .graph-nodes,
.graph .graph-edges {
    fill: #aaa;
}

.graph-edges {
    stroke: #aaa;
    stroke-width: 3;
}

.graph-edges rect {
    stroke: none;
}

.graph-edges rect.colourable.no-fill {
    stroke-width: 2;
}

.graph-edge.inactive {
    fill: #aaa;
}

.node-outline {
    fill: none;
    stroke-width: 2;
    stroke: #fff;
}

.edge-outline {
    stroke-width: 3;
    stroke: #fff;
}

.symmetry-line {
    stroke: #222;
    stroke-dasharray: 2 2;
}
